<template>
	<view class="box">
		<view class="item" v-for="(item,index) in list" :key="index">
			<view class="title">
				<view class="">订单编号：{{item.OrderNo}}</view>
				<view class="status">{{statusList[item.Status]}}</view>
			</view>
			<view class="image-list">
				<image v-if="ite !== ''" v-for="(ite,index) in item.LaborImage" :src="`${baseUrl}${ite}`" mode="">
				</image>
				<view v-else class="">
					暂无照片
				</view>
			</view>
			<view class="content">{{item.LaborDescribe}}</view>
			<view class="time">{{item.SubmitTime}}</view>
		</view>
	</view>
</template>

<script>
	import {
		GetSelectLobarListAjax
	} from '@/apis/api/modules/user-center'
	export default {
		data() {
			return {
				list: [],
				orderId: '',
				statusList: ['待劳作', '已劳作待评价', '已完成', '已关闭'],
				baseUrl: 'https://farm.juzhentech.com/'
			}
		},
		onLoad(e) {
			this.orderId = e.orderId
			this.getData()
		},
		methods: {
			async getData() {
				const result = await GetSelectLobarListAjax({
					OrderID: this.orderId,
					token: true
				})
				this.list = result.Result.map(item => {
					return {
						LaborDescribe: item.LaborDescribe,
						LaborImage: item.LaborImage.split(','),
						OrderNo: item.OrderNo,
						Status: item.Status,
						SubmitTime: item.SubmitTime
					}
				})
				console.log(this.list, 'this.list.LaborImage')
			}
		}
	}
</script>

<style scoped lang="scss">
	.box {
		min-height: 100%;
		overflow: hidden;
		background: #F7F7F7;
		padding: 30rpx;
	}

	.item {
		padding: 30rpx;
		background: #fff;
		border-radius: 20rpx;
		margin-bottom: 30rpx;

		.title {
			display: flex;
			justify-content: space-between;
			font-size: 32rpx;
			font-weight: 400;
			color: #333333;
			line-height: 44rpx;
			border-bottom: 2rpx solid #efefef;
			padding-bottom: 20rpx;

			.status {
				width: 115rpx;
			}
		}

		.image-list {
			margin: 20rpx 0;
		}

		image {
			width: 300rpx;
			height: 300rpx;
		}

		.content {
			font-size: 28rpx;
			font-weight: 400;
			color: #666666;
			line-height: 44rpx;
		}

		.time {
			padding-top: 20rpx;
			display: flex;
			justify-content: flex-end;
		}
	}
</style>
